"use client"

import { Input } from "@/components/ui/input"
import { Button } from "@/components/ui/button"
import { Label } from "@/components/ui/label"
import { Slider } from "@/components/ui/slider"
import { Card, CardContent } from "@/components/ui/card"
import { useState } from "react"
import { calculateOption } from "./actions/calculate"
import { Loader2 } from "lucide-react"

interface CalculationResult {
  callOptionPrice: string
  putOptionPrice: string
  impliedVolatility: string
  delta: string
  timestamp: string
}

export default function OptionCalculator() {
  const [volatility, setVolatility] = useState(0.16)
  const [isCalculating, setIsCalculating] = useState(false)
  const [result, setResult] = useState<CalculationResult | null>(null)
  const [error, setError] = useState<string | null>(null)

  async function handleCalculate() {
    setIsCalculating(true)
    setError(null)

    try {
      const stockPrice = Number.parseFloat(document.getElementById("stock-price")?.value as string)
      const strikePrice = Number.parseFloat(document.getElementById("strike-price")?.value as string)
      const interestRate = Number.parseFloat(document.getElementById("interest-rate")?.value as string)
      const dividendYield = Number.parseFloat(document.getElementById("dividend-yield")?.value as string)
      const timeToExpiration = Number.parseFloat(document.getElementById("expiration-time")?.value as string)

      const inputs = {
        stockPrice,
        strikePrice,
        interestRate,
        dividendYield,
        timeToExpiration,
        volatility,
      }

      const result = await calculateOption(inputs)
      setResult(result)
    } catch (err) {
      setError("Failed to calculate option prices. Please check your inputs.")
    } finally {
      setIsCalculating(false)
    }
  }

  return (
    <div className="w-full max-w-xl mx-auto p-6 space-y-6 bg-white rounded-lg shadow-sm">
      <h1 className="text-2xl font-semibold text-center text-[#0088FF]">Option Pricing Calculator</h1>

      <div className="space-y-4">
        <div className="space-y-2">
          <Label htmlFor="stock-price">Initial Stock Price (S0):</Label>
          <Input type="number" id="stock-price" defaultValue="100" className="h-9" />
        </div>

        <div className="space-y-2">
          <Label htmlFor="strike-price">Strike Price (X):</Label>
          <Input type="number" id="strike-price" defaultValue="100" className="h-9" />
        </div>

        <div className="space-y-2">
          <Label htmlFor="interest-rate">Risk-free Interest Rate (r):</Label>
          <Input type="number" id="interest-rate" defaultValue="0.05" step="0.01" className="h-9" />
        </div>

        <div className="space-y-2">
          <Label htmlFor="dividend-yield">Dividend Yield (q):</Label>
          <Input type="number" id="dividend-yield" defaultValue="0" step="0.01" className="h-9" />
        </div>

        <div className="space-y-2">
          <Label htmlFor="expiration-time">Time to Expiration (t in years):</Label>
          <Input type="number" id="expiration-time" defaultValue="1" step="0.1" className="h-9" />
        </div>

        <div className="space-y-2">
          <Label htmlFor="volatility">Expected Volatility (v):</Label>
          <Input
            type="number"
            id="volatility"
            value={volatility}
            onChange={(e) => setVolatility(Number(e.target.value))}
            step="0.01"
            className="h-9"
          />
          <div className="pt-4">
            <Slider
              value={[volatility]}
              onValueChange={([value]) => setVolatility(value)}
              max={1}
              step={0.01}
              className="w-full"
            />
          </div>
          <div className="text-sm text-gray-600">{volatility.toFixed(2)}</div>
        </div>

        <Button
          className="w-full bg-[#0088FF] hover:bg-[#0066CC] text-white h-10 mt-6"
          onClick={handleCalculate}
          disabled={isCalculating}
        >
          {isCalculating ? (
            <>
              <Loader2 className="mr-2 h-4 w-4 animate-spin" />
              Calculating...
            </>
          ) : (
            "Calculate"
          )}
        </Button>

        {error && <div className="text-red-500 text-sm text-center">{error}</div>}

        {result && (
          <Card className="mt-6">
            <CardContent className="pt-6">
              <div className="grid grid-cols-2 gap-4">
                <div>
                  <div className="text-sm text-gray-500">Call Option Price</div>
                  <div className="text-lg font-semibold text-[#0088FF]">${result.callOptionPrice}</div>
                </div>
                <div>
                  <div className="text-sm text-gray-500">Put Option Price</div>
                  <div className="text-lg font-semibold text-[#0088FF]">${result.putOptionPrice}</div>
                </div>
                <div>
                  <div className="text-sm text-gray-500">Implied Volatility</div>
                  <div className="text-lg font-semibold">{result.impliedVolatility}</div>
                </div>
                <div>
                  <div className="text-sm text-gray-500">Delta</div>
                  <div className="text-lg font-semibold">{result.delta}</div>
                </div>
              </div>
              <div className="text-xs text-gray-400 mt-4 text-right">
                Last calculated: {new Date(result.timestamp).toLocaleString()}
              </div>
            </CardContent>
          </Card>
        )}
      </div>
    </div>
  )
}